.main {
	display: flex;
	padding: 10px 40px;
	flex-direction: column;

	.title {
		color: var(--sPrimary);
		text-align: center;
		font-size: 26px;
		font-style: normal;
		font-weight: 700;
		line-height: normal;
		margin-bottom: 50px;
	}

	.wrapper {
		display: flex;

		.left {
			width: 100%;
			max-width: 225px;
			border-right: 1px solid var(--sPrimary);
			padding-right: 20px;

			.list {
				list-style-type: none;
				.item {
					display: flex;

					&:not(:last-child) {
						margin-bottom: 20px;
					}

					span {
						width: 60%;
					}
				}
			}
		}

		.right {
			flex: 1;
			gap: 8px;
			display: flex;
			padding-left: 20px;
			flex-direction: column;

			.label {
				color: var(--sPrimary);
				font-size: 14px;
				font-style: normal;
				font-weight: 400;
				line-height: normal;
			}
		}
	}
}
